* {
  box-sizing: border-box;
  background-color: rgb(249, 249, 250);
}


button {
  border-style: none;
}

/* 메인페이지 */
.container {
  min-width: 500px;
  max-width: 1100px;
}

.justify-content-center {
  margin: 0 auto;
}

.display {
  display: flex;
  justify-content: center;
}


.space {
  padding-bottom: 27px;
}

/* aside 추천 */
.aside {
  min-width: 300px;
  padding-left: 5px;
}

.myInfo {
  display: flex;
  justify-content: space-between;
  padding-bottom: 17px;
  padding-top: 20px;
}

.myInfo button {
  font-size: 13px;
  font-weight: bold;
  color:rgb(42, 163, 255);
}

.smallInfo {
  padding-left: 15px;
}

.myInfoPic {
  width: 50px;
  height: 50px;
  border-radius: 50%;

}


/* 프로필 사진*/
.profilePic {
  width: 35px;
  height: 35px;
  border-radius: 50%;
}





